import styles from "./Name.module.css";
import IDnumber from "./IDnumber";
import Card from "./UI/Card";
const name = ({ names }) => {
  return (
    <div>
      {names.map((name) => (
        <Card key={name.ID} className={styles.name}>
          <IDnumber id={name.ID} />
          <h1>{name.Name}</h1>
          <p>|</p>
          <h2>{name.Transliteration}</h2>
          <p>|</p>
          <h3>{name.Meaning}</h3>
        </Card>
      ))}
    </div>
  );
};

export default name;
